<template>
  <section>
    <SubTitle :title="'History of ' +selectedTab"/>
    <SumContainer>
      <SumItem :label="`min (${min.date||''})`" :number="min[selectedTab] | numFormat(' 0,0.00')"/>
      <SumItem :label="`max (${max.date||''})`" :number="max[selectedTab] | numFormat(' 0,0.00')"/>
<!--      <SumItem label="items" :number="computedInitialList.length"/>-->
    </SumContainer>
    <div class="fc" style="margin: 50px 0">
      <div class="pr">
        <trend
          :data="computedArrForGraph"
          :gradient="selectedTab==='totalExpenses'?['red', 'white', 'green']:['green', 'white', 'red']"
          auto-draw
          :autoDrawDuration="700"
          :stroke-width="0.5"
          smooth
          class="card trendContainer"
        ></trend>
        <div class="hAmount">{{computedInitialList.length}}</div>
      </div>
    </div>
    <div class="card p5">
      <FilterBlock items-name="history" :list="computedInitialList" :selected-item.sync="selectedTab"/>
    </div>
    <ul class="card p10" style="margin-bottom: 50px">
      <form @submit.prevent="filterHistory" class="reduceContainer">
        <input
          type="text"
          v-model.number="filterByNumber"
          style="background-color:transparent; color:rgba(255, 255, 255, 0.5); width:200px"
          placeholder="reduce items by $ difference"
          v-on:keyup.enter="(ev) => ev.target.blur()"
        >
      </form>
      <HistoryListItem
        v-for="item in computedFinalChunkedList"
        :key="item.id"
        :item="item"
        :selected-tab.sync="selectedTab"
      />
      <paginate
        :page-count="cPageCount"
        :page-range="3"
        :margin-pages="2"
        :click-handler="clickPage"
        :prev-text="'Prev'"
        :next-text="'Next'"
        :container-class="'paginationContainer'"
        :page-class="'pageNumber'">
      </paginate>
      <div class="text-center">
<!--        <v-pagination-->
<!--          v-model="page"-->
<!--          :length="15"-->
<!--          :total-visible="7"-->
<!--          color="#F8CC2D"-->
<!--        ></v-pagination>-->
      </div>
    </ul>
  </section>
</template>

<script>
import SubTitle from '../../components/SubTitle';
import SumItem from '../../components/sum/SumItem';
import SumContainer from '../../components/sum/SumContainer';
import HistoryListItem from '../../components/HistoryListItem';
import FilterBlock from '../../components/AltsList/FilterBlock';
import FilterBlockLabels from '../../components/AltsList/FilterBlockLabels';
  export default {
    components: {
      FilterBlockLabels,
      FilterBlock,
      HistoryListItem,
      SumItem,
      SumContainer,
      SubTitle,
    },
    props:['list'],
    data() {//локальн стейт
      return {
        selectedTab:'totalNow',
        filterByNumber:null,
        page:0,
        chunkSize:20,
      };
    },
    computed: {
      min(){//проблема с this 2 этажа, поэтому как аргумент
        if(this.computedInitialList.length){
          const of=this.selectedTab;
          return this.computedInitialList.reduce(function(prev, current) {
            return (prev[of] < current[of]) ? prev : current
          })//return object
        }else return 0
      },
      max(){//проблема с this 2 этажа, поэтому как аргумент
        if(this.computedInitialList.length){
          const of=this.selectedTab;
          return this.computedInitialList.reduce(function(prev, current) {
            return (prev[of] > current[of]) ? prev : current
          })
        }else return 0
      },
      cPageCount(){
        return Math.floor(this.computedInitialList.length/this.chunkSize)
      },
      computedArrForGraph(){
        if(this.list&&this.list.length){
          return this.list.map(item=>item[this.selectedTab])
        }else return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].historyList.map(item=>item[this.selectedTab])||[]
      },
      computedFinalChunkedList(){//лист после разделения по страницам
        const list = [...this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].historyList];
        let results = [];
        while (list.length) {
          results.push(list.splice(0, this.chunkSize));
        }
        return results[this.page]
      },
      computedInitialList(){//первоначальный лист до разделения по страницам
        // console.log(this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].historyList)
        if(this.list&&this.list.length){
          return this.list
        }else return this.$store.state[this.$store.state.isAK?'infoAK':'infoMain'].historyList||[]
      }
    },

    methods: {
      clickPage(pageNum) {
        this.page = pageNum
      },
      filterHistory(){
        this.$store.commit('filterHistory',{diff:this.filterByNumber});
        this.filterByNumber=null;
      },
    },
  };
</script>

